<template>
  <div>
    <div class="todo">
      <h1 class="tail text-grey-500 text-xl my-3">Mis Pendientes</h1>
      <div>
        <input v-model="tarea" type="text" id="newTask" class="border outline-none text-black rounded mr-3 py-1 pl-2 focus:bg-purple-200 md:w-4/12">
        <button class="bg-green-300 px-3 py-1 text-white rounded" @click="addTask">Agregar</button>
      </div>
      <!-- TAREAS -->
      <div  id="tareas" class="border border-green-200 p-1 w-10/12 h-auto m-auto mt-5">
        <!-- TAREA -->
        <div v-for="(tarea, index) of lista" :key="index"  class="tarea flex justify-between items-center px-2 h-auto border rounded mt-1 " :class="['bg', tarea.estado ? 'bg-green-300' : 'bg-red-300' ]">
        <div>
            <i class="fas fa-ellipsis-v"></i>
        </div>
        <div class="borde w-4/6">
            <p class="text-black my-2 text-lg flex break-words break-all">{{index}}: {{tarea.nombre}}</p>
        </div>
        <div class="actions h-full w-14 flex justify-around items-center">
            <i class="fas fa-trash-alt text-red-600 opacity-50 hover:opacity-100 " @click="eliminar(index)"></i>
            <i class="fas fa-check text-green-600 opacity-50 hover:opacity-100" @click="completada(index)"></i>
        </div>
      </div>
        <!-- TAREA -->
      </div>
      <!-- / TAREAS -->

    </div>
  </div>
</template>

<script>

export default {
  
  name: 'navBar',
  components:{},
  data(){
    return{
      lista:[],
      tarea:""
    }
  },
  methods:{
    addTask(){
      if(this.tarea == ""){
        alert(" Escribe una nueva tarea ")
      } else {
      alert("Tarea Agregada");
      this.lista.push(
        { nombre:this.tarea, estado: false }
      );
      this.saveInLocalStorage();
      document.getElementById("newTask").focus();
      this.tarea="";
      }
    },
    saveInLocalStorage(){
      localStorage.setItem("lista_tareas",JSON.stringify(this.lista));
    },
    eliminar(index){
      this.lista.splice(index, 1);
      this.saveInLocalStorage();
    },
    completada(index){
      this.lista[index].estado = true;
      this.saveInLocalStorage();
    }, 
  },
  created(){
    this.saveInLocalStorage()
    let LocalDB = JSON.parse(localStorage.getItem("lista_tareas"))
    if(LocalDB !== null){
      this.lista = LocalDB
    }else{
      this.lista = ""
    }
  }   
}
</script>

